.ProductForm label {
  display: block;
  margin-bottom: 10px;
}

.ProductForm input,
.ProductForm select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.ProductForm button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
}

/* Product List */
.ProductList table {
  width: 100%;
  border-collapse: collapse;
}

.ProductList th,
.ProductList td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.ProductList th {
  background-color: #f2f2f2;
}

.ProductList button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

